<template>
	<view class="my_code">
		<!-- 程序码 -->
		<view class="code_img"><image src="../../static/lv.jpg" mode="widthFix"></image></view>
		<!-- 保存到相册，扫一扫 -->
		<view class="btm">
			<view class="le" @click="savePhotoAlbum">
				<view class="iconfont iconxiazai"></view>
				<view class="">保存到相册</view>
			</view>
			<view class="ri" @click="goScan">
				<view class="iconfont iconsaoyisao"></view>
				<view class="">扫一扫</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			myCode: ''
		};
	},
	onLoad() {
		this.getAccessToken();
	},
	methods: {
		// 扫一扫
		goScan(){
			uni.scanCode({
				success: function (res) {
					console.log('res',res)
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			});
		},
		// 保存到相册
		savePhotoAlbum() {
			//判断用户授权
			uni.showModal({
				title: '是否下载当前图片',
				success(res) {
					if (res.confirm) {
						uni.downloadFile({
							// 二维码地址
							url: 'https://dss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1819216937,2118754409&fm=26&gp=0.jpg',
							success: res => {
								console.log('downloadFile成功', res);
								// wx.showLoading({
								//   title: '获取资源中',
								// })
								uni.saveImageToPhotosAlbum({
									filePath: res.tempFilePath,
									success: file => {
										console.log('saveVideoToPhotosAlb、um成功', file);
										wx.hideLoading();
										wx.showToast({
											title: '下载成功',
											icon: 'none'
										});
									},
									fail: err => {
										console.log('saveVideoToPhotosAlbum失败', err);
										if (err.errMsg == 'saveImageToPhotosAlbum:fail auth deny') {
											wx.showModal({
												title: '提示',
												content: '需要您授权保存相册',
												showCancel: false,
												success: data => {
													wx.openSetting({
														success(settingdata) {
															if (settingdata.authSetting['scope.writePhotosAlbum']) {
																wx.showModal({
																	title: '提示',
																	content: '获取权限成功,再次点击下载即可保存',
																	showCancel: false
																});
															} else {
																wx.showModal({
																	title: '提示',
																	content: '获取权限失败，将无法保存到相册哦~',
																	showCancel: false
																});
															}
														}
													});
												}
											});
										}
									}
								});
							},
							fail: res => {
								console.log('downloadFile失败', res);
							}
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				},
				fail() {
					uni.showToast({
						icon: 'none',
						title: '下载失败'
					});
				}
			});
		},
		// 通过发送请求，后台获取返回ACCESS_TOKEN
		getAccessToken() {
			wx.request({
				url: 'https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=APPID&secret=AppSecret',
				method: 'get',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success: res => {
					console.log(res.data.access_token);
				}
			});
		},
		// 获取小程序码并渲染
		getCode() {
			let that = this;
			wx.request({
				url: 'https://api.weixin.qq.com/cgi-bin/wxaapp/createwxaqrcode?access_token=' + res.data.access_token,
				method: 'POST',
				header: {
					'content-type': 'application/json' // 默认值
				},
				data: {
					path: '/page/index/index?openid=11', // 携参数openid
					width: 100 //非必填
				},
				responseType: 'arraybuffer', // arraybuffer是以数组的语法处理二进制数据，称为二进制数组。
				success: function(res) {
					let data = wx.arrayBufferToBase64(res.data);
					console.log('data:image/png;base64,' + data);
					that.setData({
						myCode: 'data:image/png;base64,' + data
					});
				}
			});
		}
	}
};
</script>

<style>
page {
	width: 100%;
	height: 100%;
	/* background-color: #0000FF; */
}
</style>

<style lang="less" scoped>
.my_code {
	width: 100%;
	height: 100%;
	// 二维码
	.code_img {
		width: 80%;
		height: 60%;
		margin: 60rpx auto;
		overflow: hidden;
		image {
			border-radius: 30rpx;
			width: 100%;
			height: 100%;
		}
	}
	// 底部图标
	.btm {
		width: 74%;
		position: fixed;
		bottom: 73rpx;
		left: 50%;
		transform: translate(-50%);
		display: flex;
		justify-content: space-between;
		align-items: center;
		.le {
			text-align: center;
		}
		.ri {
			text-align: center;
		}
	}
}
.iconfont {
	font-size: 75rpx;
	margin-bottom: 20rpx;
}
</style>
